import '../assets/fontIcon/iconfont.css'
import './common/dom.js'

// tip  v-tip
import Vtip from './tips/index'
// 
import lang from './common/lang/index';

//   .vue
const requireComponent = require.context('./', true, /\.vue$/);
const install = Vue => {
    //   v-lazyload
    let lazyload = {
        install(vue, payload) {
            // 
            if (!Array.prototype.remove) {
                Array.prototype.remove = function (item) {
                    if (!this.length) return
                    let index = this.indexOf(item);
                    if (index > -1) {
                        this.splice(index, 1);
                        return this
                    }
                }
            }

            // 
            let defaultImage = 'data:image/gif;base64,'
            let errorImage = 'data:image/jpeg;base64,'

            // 10px
            let distanece = 10;
            // 
            let listenList = [];
            // 
            let imageCacheList = [];

            // 
            const isAlredyLoad = (imageSrc) => {
                if (imageCacheList.indexOf(imageSrc) > -1) {
                    return true;
                } else {
                    return false;
                }
            }

            //
            const isCanShow = (item) => {
                let ele = item.ele;
                let src = item.src;
                //
                let top = ele.getBoundingClientRect().top;
                //
                // let windowHeight = window.innerHight;
                // top - distance distance
                if (top - distanece < window.innerHeight) {
                    let image = new Image();
                    image.src = src;
                    image.onload = function () {
                        ele.src = src;
                        imageCacheList.push(src);
                        listenList.remove(item);
                    }
                    image.onerror = function () {
                        ele.src = errorImage;
                        imageCacheList.push(src);
                        listenList.remove(item);
                    }
                    return true;
                } else {
                    return false;
                }
            };

            const onListenScroll = () => {
                window.addEventListener('scroll', function () {
                    let length = listenList.length;
                    for (let i = 0; i < length; i++) {
                        isCanShow(listenList[i]);
                    }
                })
            }

            //Vue 
            const addListener = (ele, binding) => {
                //
                let imageSrc = binding.value;

                // src
                if (isAlredyLoad(imageSrc)) {
                    ele.src = imageSrc;
                    return false;
                }

                let item = {
                    ele: ele,
                    src: imageSrc
                }

                //
                ele.src = defaultImage;

                //
                if (isCanShow(item)) {
                    return
                }

                //lisenList
                listenList.push(item);

                //scroll
                onListenScroll();
            }

            Vue.directive('lazyload', {
                inserted: addListener,
                updated: addListener
            })
        }
    }
    // 
    Vue.use(Vtip.directive);
    // 
    Vue.use(lazyload);

    Vue.directive('focus', {
        inserted(el) {
            if (el.className.indexOf('zoom-input') > -1) {
                let elem = el.children;
                if (el.className.indexOf('zoom-search') > -1) {
                    elem = el.children[0].children;
                }
                for (var i = 0; i < elem.length; i++) {
                    const nodeName = elem[i];
                    if (nodeName.tagName === 'INPUT') {
                        nodeName.focus();
                        break;
                    }
                }
            } else {
                el.focus();
            }
        },
        update(el, value) {
            if (value) {
                el.focus();
            }
        }
    })
    Vue.directive('color', {
        bind(el, binding) {
            el.style.color = binding.value
        }
    })
    Vue.directive('drag', {
        inserted: function (el) {
            el.onmousedown = function (e) {
                let offsetX = e.pageX - el.offsetLeft;
                let offsetY = e.pageY - el.offsetTop;

                document.onmousemove = function (e) {
                    el.style.left = e.pageX - offsetX + 'px';
                    el.style.top = e.pageY - offsetY + 'px';
                    el.style.cursor = 'grab';
                }

                document.onmouseup = function () {
                    document.onmousemove = document.onmouseup = null;
                }
            }
        }
    })
    /**
     * @description: 
     * @param {*}   v-longPress:[time] || v-longPress:2000
     * @return {*}
     */
    Vue.directive('longPress', {
        bind(el, binding) {
            if (typeof binding.value !== 'function') {
                throw 'callback must be a function'
            }
            let time = 2000;
            if (binding.arg) {
                time = binding.arg
            }
            // 
            let pressTimer = null
            //  time 
            let start = (e) => {
                if (e.type === 'click' && e.button !== 0) {
                    return
                }
                if (pressTimer === null) {
                    pressTimer = setTimeout(() => {
                        handler()
                    }, time)
                }
            }
            // 
            let cancel = (e) => {
                if (pressTimer !== null) {
                    clearTimeout(pressTimer)
                    pressTimer = null
                }
            }
            // 
            const handler = (e) => {
                binding.value(e)
            }
            // 
            el.addEventListener('mousedown', start)
            el.addEventListener('touchstart', start)
            // 
            el.addEventListener('click', cancel)
            el.addEventListener('mouseout', cancel)
            el.addEventListener('touchend', cancel)
            el.addEventListener('touchcancel', cancel)
        },
        // 
        componentUpdated(el, { value }) {
            el.$value = value
        },
        // 
        unbind(el) {
            el.removeEventListener('click', el.handler)
        },
    })
    /**
     * @function: 
     * @description: v-waterMarker="{text:'zoom-UI',textColor:'#ff4d4f', font: '10px Microsoft YaHei'}"
     * @param {String}   text 
     * @param {String}   textColor   rgba(100, 100, 100, 0.5)
     * @param {String}   font   italic bold 16px Microsoft YaHei
     * @return {*}
     */
    Vue.directive('waterMarker', {
        bind(el, binding) {
            let addWaterMarker = (str, parentNode, font, textColor) => {
                // 
                var can = document.createElement('canvas')
                parentNode.appendChild(can)
                can.width = 200
                can.height = 150
                can.style.display = 'none'
                var cans = can.getContext('2d')
                cans.rotate((-20 * Math.PI) / 180)
                cans.font = font || 'italic bold 16px Microsoft YaHei'
                cans.fillStyle = textColor || 'rgba(100, 100, 100, 0.5)'
                cans.textAlign = 'left'
                cans.textBaseline = 'Middle'
                cans.fillText(str, can.width / 10, can.height / 2)
                parentNode.style.backgroundImage = 'url(' + can.toDataURL('image/png') + ')'
            }
            addWaterMarker(binding.value.text, el, binding.value.font, binding.value.textColor)
        }
    })
    // 
    Vue.prototype.$zoom = {
        /**
         * @function: 
         * @description: , ,  this.$zoom.copy(msg)
         * @param {*} value
         * @return {*}  value
         */
        copy(value) {
            if (!value) {
                return
            }
            //  textarea 
            const textarea = document.createElement('textarea')
            //  textarea  readonly  iOS  textarea 
            textarea.readOnly = 'readonly'
            textarea.style.position = 'absolute'
            textarea.style.left = '-9999px'
            //  copy  textarea  value 
            textarea.value = value
            //  textarea  body 
            document.body.appendChild(textarea)
            // 
            textarea.select()
            const result = document.execCommand('Copy');
            if (result) {
                return textarea.value;
            }
            document.body.removeChild(textarea)
        },
        /**
         * @function: 
         * @description: url,  cookie.language, 
         * @param {*}
         * @return {*}
         */
        getLanguage() {
            let lang = 'zh';
            if (this.cookie.getChild("language", 'locale')) {
                lang = this.cookie.getChild("language", 'locale');
            }
            return { locale: lang, i18n: this.LanguageInfo };
        },
        /**
         * @function: 
         * @description: , 
         * @param {object} lang {locale: 'zh', i18n: {zh: {'xx': 'xx'}, en: {'xx': 'xx'}}
         * @return {*}  this.getLanguage()
         */
        setLanguage(lang) {
            if (lang.locale) {
                this.cookie.set("language", { locale: lang.locale });
                //  ,keythis.LanguageInfo
                if (lang.i18n && Object.prototype.toString.call(lang.i18n) === '[object Object]') {
                    for (let key in lang.i18n) {
                        this.LanguageInfo[key] = Object.assign({}, lang.i18n[key], this.LanguageInfo[key])
                    }
                }
            }
            return this.getLanguage();
        },
        //  
        LanguageInfo: lang,
        /**
         * @function: 
         * @description: this.$zoom.$t(String)
         * @param {string} val
         * @param {object} parameter $zoom.$t('xxx', {count1: 'xx', count2: 'xx'})
         * @return {*}
         */
        $t(val, parameter) {
            if (val && val.length && val.length > 0) {
                const language = this.cookie.getChild("language", 'locale') || this.getLanguage().locale;
                if (!val && !language) {
                    return;
                }
                if (this.LanguageInfo[language] && this.LanguageInfo[language][val]) {
                    let value = this.LanguageInfo[language][val];
                    // parameter, parameterkey
                    if (parameter && Object.prototype.toString.call(parameter) === '[object Object]') {
                        for (let key in parameter) {
                            value = value.replace(`{${key}}`, parameter[key]);
                        }
                    }
                    return value;
                }
            }
        },
        /**
         * @function: 
         * @description: dateFormat("YYYY-mm-dd HH:MM", date)
         * @param {String} fmt
         * @param {date} date
         * @return {date}
         */
        dateFormat(fmt, date) {
            date = new Date(date);
            let ret;
            let opt = {
                "Y+": date.getFullYear().toString(),        // 
                "m+": (date.getMonth() + 1).toString(),     // 
                "d+": date.getDate().toString(),            // 
                "H+": date.getHours().toString(),           // 
                "M+": date.getMinutes().toString(),         // 
                "S+": date.getSeconds().toString()          // 
                // 
            };
            for (let k in opt) {
                ret = new RegExp("(" + k + ")").exec(fmt);
                if (ret) {
                    fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
                }
            }
            return fmt;
        },
        /**
         * @function: 
         * @description: , 
         * @time 2019111121:18:38
         * @param {object, Array} obj || Arr
         * @return {*}
         */
        clone(obj) {
            let clonedObj;
            // 
            if (['number', 'string', 'boolean', 'undefined', 'symbol',].includes(typeof obj)
                || obj === null) {
                clonedObj = obj;
                return clonedObj;
            }
            const constructor = obj.constructor || Object;
            clonedObj = new constructor();
            Object.entries(obj).forEach(([key, value]) => {
                clonedObj[key] = this.clone(value);
            })
            return clonedObj;
        },
        /**
         * @function: Dom id
         * @description: iddom 
         * @param {String} id
         * @return {*}
         */
        $id(id) {
            return document.getElementById(id);
        },
        /**
         * @function: 
         * @description: , 
         * @param {Number} min
         * @param {Number} max
         * @return {*}
         */
        $rn(min, max) {
            let n = Math.random() * (max - min) + min;
            return Math.floor(n)
        },
        /**
         * @function: 
         * @description: , , min0, max255
         * @param {Number} min
         * @param {Number} max
         * @return {*}
         */
        $rc(min, max) {
            if (!min || min < 0) {
                min = 0;
            }
            if (!max || max > 255) {
                max = 255;
            }
            let r = this.$rn(min, max);
            let g = this.$rn(min, max);
            let b = this.$rn(min, max);
            return `rgb(${r},${g},${b})`
        },
        /**
         *  cookie 
         *  set cookie  eg : this.$zoom.cookie.set("info", { name : "zoom", age : 23});
         *  setChild    cookie    eg : this.$zoom.cookie.setChild("info", "sex", "boy");
         *  get  /  cookie  eg : this.$zoom.cookie.get("info");
         *  getChild    cookie    eg : this.$zoom.cookie.getChild("info", "name");
         *  del cookie      eg : this.$zoom.cookie.del("info");
         *  delChild    cookie      eg : this.$zoom.cookie.delChild("info", "name");
         */
        cookie: {
            /**
             * @function: cookie
             * @description: this.$zoom.cookie.set("info", { name : "zoom", age : 23});
             * @param {String} name  cookie
             * @param {Object} subCookies  cookie
             * @param {String} expires   cookie
             * @param {String} domain  cookie
             * @param {String} path  cookie
             * @param {String} secure cookie
             * @return {*}
             */
            set(name, subCookies, expires, domain, path, secure) {
                if (!name) {
                    throw Error('zoom-ui SyntaxError: cookie! ')
                }
                let cookieText = "", subName, cookieParts = [];
                cookieText += encodeURIComponent(name) + "=";
                for (subName in subCookies) {
                    cookieParts.push(encodeURIComponent(subName) + "=" + encodeURIComponent(subCookies[subName]));
                }
                if (cookieParts.length > 0) {
                    cookieText += cookieParts.join("&");
                    if (expires instanceof Date) {
                        cookieText += "; expires=" + expires.toGMTString();
                    }
                    if (path) {
                        cookieText += "; path=" + path;
                    }
                    if (domain) {
                        cookieText += "; domain=" + domain;
                    }
                    if (secure) {
                        cookieText += "; secure";
                    }
                } else {
                    cookieText += "; expires=" + new Date(0).toGMTString();
                }
                document.cookie = cookieText;
            },
            /**
             * @function: cookie
             * @description: this.$zoom.cookie.setChild("info", "sex", "boy");
             * @param {String} name  cookie
             * @param {String} subName  cookie
             * @param {String} value  cookie
             * @param {String} expires   cookie
             * @param {String} domain  cookie
             * @param {String} path  cookie
             * @param {String} secure cookie
             * @return {*}
             */
            setChild(name, subName, value, expires, domain, path, secure) {
                if (!name || !subName || !value) {
                    throw Error('zoom-ui SyntaxError: cookiecookiecookie! ')
                }
                let cookies = this.get(name) || {};
                cookies[subName] = value;
                this.set(name, cookies, expires, domain, path, secure);
            },
            /**
             * @function: cookie
             * @description: cookie this.$zoom.cookie.get("info");
             * @param {*} name cookiecookie
             * @return {Object}
             */
            get(name) {
                if (!name) {
                    return document.cookie;
                }
                let cookieName = encodeURIComponent(name) + "=",
                    cookieStart = document.cookie.indexOf(cookieName),
                    cookieValue = "", i, len, subCookies, parts, result = {};
                if (cookieStart > -1) {
                    let cookieEnd = document.cookie.indexOf(";", cookieStart);
                    if (cookieEnd == -1) {
                        cookieEnd = document.cookie.length;
                    }
                    cookieValue = decodeURIComponent(document.cookie.substring(cookieStart + cookieName.length, cookieEnd));
                    if (cookieValue.length > 0) {
                        subCookies = cookieValue.split("&");
                        for (i = 0, len = subCookies.length; i < len; i++) {
                            parts = subCookies[i].split("=");
                            result[decodeURIComponent(parts[0])] = decodeURIComponent(parts[1]);
                        }
                        return result;
                    }
                }
                return null;
            },
            /**
             * @function: cookie
             * @description: this.$zoom.cookie.getChild("info", "name");
             * @param {String} name  cookie
             * @param {String} subName   cookie
             * @return {String}  cookie
             */
            getChild(name, subName) {
                if (!name || !subName) {
                    throw Error('zoom-ui SyntaxError: cookiecookie! ')
                }
                let cookies = this.get(name);
                if (cookies) {
                    return cookies[subName];
                } else {
                    return null;
                }
            },
            /**
             * @function: cookie
             * @description: this.$zoom.cookie.del("info");
             * @param {String} name  cookie
             * @param {String} domain  cookie
             * @param {String} path  cookie
             * @param {String} secure cookie
             * @return {*}
             */
            del(name, domain, path, secure) {
                if (!name) {
                    throw Error('zoom-ui SyntaxError: cookie! ')
                }
                this.set(name, "", new Date(0).toGMTString(), domain, path, secure);
            },
            /**
             * @function: cookie
             * @description:
             * @param {String} name  cookie
             * @param {String} subName   cookie
             * @param {String} domain  cookie
             * @param {String} path  cookie
             * @param {String} secure cookie
             * @return {*}
             */
            delChild(name, subName, domain, path, secure) {
                if (!name || !subName) {
                    throw Error('zoom-ui SyntaxError: cookiecookie! ')
                }
                let cookies = this.get(name);
                if (cookies) {
                    delete cookies[subName];
                    this.set(name, cookies, null, domain, path, secure);
                }
            },
            /**
             * @function: cookie
             * @description: this.$zoom.cookie.clear();
             * @param {*}
             * @return {*}
             */
            clear() {
                let keys = document.cookie.match(/[^ =;]+(?==)/g)
                if (keys) {
                    for (let i = keys.length; i--;) {
                        document.cookie = keys[i] + '=0;path=/;expires=' + new Date(0).toUTCString() // ,m.zoom.cn
                        document.cookie = keys[i] + '=0;path=/;domain=' + document.domain + ';expires=' + new Date(0).toUTCString() //  .m.zoom.cn
                        document.cookie = keys[i] + '=0;path=/;domain=ratingdog.cn;expires=' + new Date(0).toUTCString() //  .zoom.cn
                    }
                }
            }
        },
        // tip
        tip: Vtip.tip
    };
    if (install.installed) return;
    install.installed;
    requireComponent.keys().map(component => {
        // 
        const config = requireComponent(component);
        // 
        const componentName = config.default.name;
        // 
        switch (componentName) {
            case 'zoom-popup':
                // eslint-disable-next-line no-case-declarations
                const Popup = config.default;
                // eslint-disable-next-line no-case-declarations
                const PopupBox = Vue.extend(Popup);
                Popup.install = function (data) {
                    let instance = new PopupBox({
                        data
                    }).$mount()

                    document.body.appendChild(instance.$el)

                    Vue.nextTick(() => {
                        // visible visible
                        instance.visible = true
                    })
                }
                Vue.prototype.$zoom.popup = Popup.install;
                break;
            case 'zoom-alert':
                // eslint-disable-next-line no-case-declarations
                const Alert = config.default;
                // eslint-disable-next-line no-case-declarations
                const AlertBox = Vue.extend(Alert);
                Alert.install = function (data) {
                    let instance = new AlertBox({
                        data
                    }).$mount()

                    document.body.appendChild(instance.$el)

                    Vue.nextTick(() => {
                        instance.show = true
                        // show show
                    })
                }
                Vue.prototype.$zoom.alert = Alert.install;
                break;
            case 'zoom-confim':
                // eslint-disable-next-line no-case-declarations
                const confim = config.default;
                // eslint-disable-next-line no-case-declarations
                const ConfirmBox = Vue.extend(confim);
                confim.install = (content, title, options) => {
                    if (typeof title === 'object') {
                        options = title;
                        title = '';
                    } else if (title === undefined) {
                        title = '';
                    }
                    if (typeof content === 'object') {
                        options = content;
                        content = '';
                    } else if (content === undefined) {
                        content = '';
                    }

                    options = Object.assign({
                        title: title,
                        content: content,
                    }, options);

                    let instance = new ConfirmBox({
                        data: options
                    }).$mount();

                    document.body.appendChild(instance.$el);

                    return instance.confirm();
                };
                Vue.prototype.$zoom.confim = confim.install;
                break;
            case 'zoom-prompt':
                // eslint-disable-next-line no-case-declarations
                const prompt = config.default;
                // eslint-disable-next-line no-case-declarations
                const PromptBox = Vue.extend(prompt);
                prompt.install = (content, title, options) => {
                    if (typeof title === 'object') {
                        options = title;
                        title = '';
                    } else if (title === undefined) {
                        title = '';
                    }
                    if (typeof content === 'object') {
                        options = content;
                        content = '';
                    } else if (content === undefined) {
                        content = '';
                    }

                    options = Object.assign({
                        title: title,
                        content: content,
                    }, options);

                    let instance = new PromptBox({
                        data: options
                    }).$mount();

                    document.body.appendChild(instance.$el);

                    return instance.confirm();
                };
                Vue.prototype.$zoom.prompt = prompt.install;
                break;
            case 'zoom-loading':
                // eslint-disable-next-line no-case-declarations
                let Loading = {}
                // installflag
                Loading.installed = false
                Loading.install = function (Vue) {
                    if (Loading.installed) return
                    Vue.prototype.$zoom.loading = {
                        show(data) {
                            // loading
                            if (document.getElementById('zoom-loading')) return
                            // 1loading
                            let LoadingTip = Vue.extend(config.default)
                            // 2 data 
                            let tpl = new LoadingTip({ data }).$mount().$el
                            // 3body
                            document.body.appendChild(tpl)
                            Loading.installed = true
                        },
                        hide() {
                            const tpl = document.getElementById('zoom-loading')
                            document.body.removeChild(tpl)
                        }
                    }
                }
                Vue.use(Loading);
                break;
            default:
                break;
        }
        Vue.component(componentName, config.default || config);
    })
}

export default {
    install
}